html {
  background-color:#e6eeee;
  background-size: cover;    
}

.app {
  width: 100%;
}

.app__content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}

.search__box {
  background: #ffa335;
  opacity: 0.85;
}

.block {
  display: block !important;
}

@media only screen 
  and (min-device-width: 250px) 
  and (max-device-width: 480px) {
  .app__content {
    display: block;
  }
}
@media only screen and (max-width: 768px) {
    /* For desktop: */
    .app__content {
    display: block;
  }
}




